.camera {
  margin-bottom: 20px;

  input {
    position: absolute;
    left: -9999px;
  }

  label {
    display: block;
    width: 150px;
    height: 50px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 36px;
    box-shadow: 2px 5px 10px var(--color-smoke);
    color: var(--color-snow);
    background: var(--color-ocean);
    text-align: center;
    cursor: pointer;
    line-height: 50px;
  }
}

.preview {
  display: block;
  width: 300px;
  margin: 50px auto;
  box-shadow: 2px 5px 10px var(--color-smoke);
}
